<template>
  <div class="hour72nutrition">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>72小時營養篩檢表</span>
            </h2>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-form ref="form">
          <v-card>
            <v-card-text>
              <v-row>
                <v-col cols="12" md="6">
                  <v-text-field type="date" label="評估日期" v-model="list.date" :rules="emptyRules"></v-text-field>
                </v-col>
                <v-col cols="12" md="6" class="text-right">
                  <v-btn @click="print" class="primary">
                    列印
                    <v-icon class="mx-1">mdi-printer</v-icon>
                  </v-btn>
                </v-col>
                <v-col cols="12">
                  <v-alert color="primary" border="left" outlined>
                    <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                      <h2>1.體重變化</h2>
                    </v-sheet>

                    <v-row>
                      <v-col cols="3">入住身高 {{uData.height}} cm</v-col>
                      <v-col cols="3">入住體重 {{uData.weight}} kg</v-col>
                      <v-col cols="3">BMI {{bmi}} kg/m<sup>2</sup></v-col>
                      <v-col cols="3">理想體重 {{ibw}} kg</v-col>
                    </v-row>
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" border="left" outlined>
                    <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                      <h2>2.飲食攝取</h2>
                    </v-sheet>
                    <v-row>
                      <v-col cols="12">(1)飲食種類:
                        <v-row>
                          <v-col cols="8">
                            <v-row justify="space-around">
                              <v-checkbox label="葷食" v-model="nursingassessmentData.diet1" value="true" ></v-checkbox>
                              <v-checkbox label="早素" v-model="nursingassessmentData.diet2" value="true" ></v-checkbox>
                              <v-checkbox label="初一、十五早素" v-model="nursingassessmentData.diet3" value="true" ></v-checkbox>
                              <v-checkbox label="初一、十五全素" v-model="nursingassessmentData.diet4" value="true" ></v-checkbox>
                            </v-row>
                          </v-col>
                          <v-col cols="4">
                            <v-text-field label="其他內容" v-model="nursingassessmentData.diet5" ></v-text-field>
                          </v-col>
                        </v-row>
                      </v-col>
                      <v-col cols="12">(2)飲食型態:
                        <v-row>
                          <v-col cols="8">
                            <v-row justify="space-around">
                              <v-checkbox label="普通" v-model="nursingassessmentData.diet6" value="true" ></v-checkbox>
                              <v-checkbox label="軟質" v-model="nursingassessmentData.diet7" value="true" ></v-checkbox>
                              <v-checkbox label="細碎" v-model="nursingassessmentData.diet8" value="true" ></v-checkbox>
                              <v-checkbox label="半流質" v-model="nursingassessmentData.diet9" value="true" ></v-checkbox>
                              <v-checkbox label="全流質" v-model="nursingassessmentData.diet10" value="true" ></v-checkbox>
                              <v-checkbox label="管灌" v-model="nursingassessmentData.diet11" value="true" ></v-checkbox>
                              <v-checkbox label="素食" v-model="nursingassessmentData.diet12" value="true" ></v-checkbox>
                            </v-row>
                          </v-col>
                          <v-col cols="4">
                            <v-text-field label="其他內容" v-model="nursingassessmentData.diet13" ></v-text-field>
                          </v-col>
                        </v-row>
                      </v-col>
                      
                      <v-col cols="12">(3)進食型態:
                        <v-combobox :items="eatingpatternItems" label="進食型態" v-model="list.eatingpattern"></v-combobox>
                      </v-col>
                      <v-col cols="12">(4)食物攝取量(與供應量比較):
                        <v-row>
                          <v-col cols="3">
                            <v-combobox :items="mealItems" label="早餐" v-model="list.breakfast"></v-combobox>
                          </v-col>
                          <v-col cols="3">
                            <v-combobox :items="mealItems" label="午餐" v-model="list.lunch"></v-combobox>
                          </v-col>
                          <v-col cols="3">
                            <v-combobox :items="mealItems" label="晚餐" v-model="list.dinner"></v-combobox>
                          </v-col>
                          <v-col cols="3">
                            <v-combobox :items="mealItems" label="點心" v-model="list.dessert"></v-combobox>
                          </v-col>
                        </v-row>
                      </v-col>
                      <v-col cols="12">(5)飲食偏好:
                        <v-row justify="space-around">
                          <v-checkbox label="無" v-model="nursingassessmentData.diet17" value="true" ></v-checkbox>
                          <v-checkbox label="奶蛋類" v-model="nursingassessmentData.diet18" value="true" ></v-checkbox>
                          <v-checkbox label="肉類" v-model="nursingassessmentData.diet19" value="true" ></v-checkbox>
                          <v-checkbox label="蔬菜" v-model="nursingassessmentData.diet20" value="true" ></v-checkbox>
                          <v-checkbox label="海鮮" v-model="nursingassessmentData.diet21" value="true" ></v-checkbox>
                          <v-checkbox label="其他" v-model="nursingassessmentData.diet22" value="true" ></v-checkbox>
                        </v-row>
                      </v-col>
                      <v-col cols="12">(6)過敏食物:
                        <v-row justify="space-around">
                          <v-checkbox label="無" v-model="nursingassessmentData.diet23" value="true" ></v-checkbox>
                          <v-checkbox label="乳製品" v-model="nursingassessmentData.diet24" value="true" ></v-checkbox>
                          <v-checkbox label="蛋類" v-model="nursingassessmentData.diet25" value="true" ></v-checkbox>
                          <v-checkbox label="牛肉" v-model="nursingassessmentData.diet26" value="true" ></v-checkbox>
                          <v-checkbox label="麩質" v-model="nursingassessmentData.diet27" value="true" ></v-checkbox>
                          <v-checkbox label="海鮮" v-model="nursingassessmentData.diet28" value="true" ></v-checkbox>
                          <v-checkbox label="其他" v-model="nursingassessmentData.diet29" value="true" ></v-checkbox>
                        </v-row>
                      </v-col>
                    </v-row>
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" border="left" outlined>
                    <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                      <h2>3.腸胃道症狀（超過兩星期）</h2>
                    </v-sheet>
                    <v-row>
                      <v-col cols="9">
                        <v-row justify="space-around" class="ml-2">
                          <v-checkbox label="正常" v-model="nursingassessmentData.gastrointestinal1" value="true" ></v-checkbox>
                          <v-checkbox label="食慾增加" v-model="nursingassessmentData.gastrointestinal2" value="true" ></v-checkbox>
                          <v-checkbox label="食慾減少" v-model="nursingassessmentData.gastrointestinal3" value="true" ></v-checkbox>
                          <v-checkbox label="咀嚼困難" v-model="nursingassessmentData.gastrointestinal4" value="true" ></v-checkbox>
                          <v-checkbox label="吞嚥困難" v-model="nursingassessmentData.gastrointestinal5" value="true" ></v-checkbox>
                          <v-checkbox label="噁心" v-model="nursingassessmentData.gastrointestinal6" value="true" ></v-checkbox>
                          <v-checkbox label="嘔吐" v-model="nursingassessmentData.gastrointestinal7" value="true" ></v-checkbox>
                          <v-checkbox label="腹脹" v-model="nursingassessmentData.gastrointestinal8" value="true" ></v-checkbox>
                          <v-checkbox label="胃不適" v-model="nursingassessmentData.gastrointestinal9" value="true" ></v-checkbox>
                          <v-checkbox label="腹水" v-model="nursingassessmentData.gastrointestinal10" value="true" ></v-checkbox>
                          <v-checkbox label="體重改變" v-model="nursingassessmentData.gastrointestinal11" value="true" ></v-checkbox>
                          <v-checkbox label="便秘" v-model="nursingassessmentData.gastrointestinal12" value="true" ></v-checkbox>
                          <v-checkbox label="腹瀉" v-model="nursingassessmentData.gastrointestinal13" value="true" ></v-checkbox>
                          <v-checkbox label="痔瘡" v-model="nursingassessmentData.gastrointestinal14" value="true" ></v-checkbox>
                          <v-checkbox label="血便" v-model="nursingassessmentData.gastrointestinal15" value="true" ></v-checkbox>
                        </v-row>
                      </v-col>
                      <v-col cols="3">
                        <v-text-field label="其他" v-model="nursingassessmentData.gastrointestinal16" class="mx-2"></v-text-field>
                      </v-col>
                    </v-row>
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" border="left" outlined>
                    <v-sheet color="blue dark-1" class="pa-2 white--text mb-2" elevation="4">
                      <h2>4.功能性評估:（依柯氏表：0為無功能不全；1-2為可走動；3為輪椅；4為臥床）</h2>
                    </v-sheet>
                    <h2>{{kbData.klv}}</h2> 
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" border="left" outlined>
                    <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                      <h2>5.生理測驗：(0=正常；1+=輕微；2+普通；3+=嚴重)</h2>
                    </v-sheet>
                    <v-row>
                      <v-col cols="4">
                        <v-combobox :items="physiologicaltestItems" label="皮下脂肪減少(肩膀、三頭肌、胸、手)" v-model="list.physiologicaltest1"></v-combobox>
                      </v-col>  
                      <v-col cols="4">
                        <v-combobox :items="physiologicaltestItems" label="肌肉耗弱(四頭肌、三角肌)" v-model="list.physiologicaltest2"></v-combobox>
                      </v-col>  
                      <v-col cols="4">
                        <v-combobox :items="physiologicaltestItems" label="下肢水腫" v-model="list.physiologicaltest3"></v-combobox>
                      </v-col>  
                      <v-col cols="4">
                        <v-combobox :items="physiologicaltestItems" label="腹水" v-model="list.physiologicaltest4"></v-combobox>
                      </v-col>  
                      <v-col cols="4">
                        <v-combobox :items="physiologicaltestItems" label="褥瘡" v-model="list.physiologicaltest5"></v-combobox>
                      </v-col>  
                      <v-col cols="4">
                        <v-text-field label="褥瘡級數" v-model="list.physiologicaltest6"></v-text-field>
                      </v-col>  
                    </v-row>
                  </v-alert>
                </v-col>

                <v-col cols="12">
                  <v-alert color="primary" border="left" outlined>
                    <v-sheet color="blue dark-1" class="pa-2 white--text" elevation="4">
                      <h2>綜合主觀性評估（A=營養足夠；B=營養不足；C=嚴重營養不足）</h2>
                    </v-sheet>
                    <v-combobox :items="assessmentItems" label="綜合主觀性評估" v-model="list.assessment"></v-combobox>
                  </v-alert>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-form>
      </v-card-text>

      <v-card-actions class="mb-10">
        <v-spacer></v-spacer>
        <v-btn class="success" @click="editOK">確認儲存</v-btn>
      </v-card-actions>
    </v-card>

    <!-- <pre>{{list}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      list: {}, //顯示的資料
      emptyRules: [v => !!v || "不可空白"],
      eatingpatternItems: ["","無人協助則無法進食", "可以自己進食但較吃力", "可以自己進食"],
      mealItems: ["","≧4/4", "3/4", "2/4","≦1/4"],
      physiologicaltestItems: ["","0=正常", "1+=輕微", "2+普通","3+=嚴重"],
      assessmentItems: ["","A=營養足夠", "B=營養不足","C=嚴重營養不足"],
      uData:[],  //病歷資料暫存
      nursingassessmentData:{},
      nursingassessmentSource:false,  //設定
      kbData:{},
      newData:false,
    };
  },

  computed:{
    bmi() {
      //BMI = 當月體重(公斤)/身高(公尺)^2
      var bmi =
        this.$store.state.uData.weight /
        ((this.$store.state.uData.height / 100) *
          (this.$store.state.uData.height / 100));
      return bmi.toFixed(2);
    },
    ibw() {
      //IBW = 身高(公尺)*身高(公尺)*22，例如1.7*1.7*22=63.58
      var ibw =
        (this.$store.state.uData.height / 100) *
        (this.$store.state.uData.height / 100) *
        22;
      return ibw.toFixed(2);
    },
  },

  mounted() {
    this.getAllData();
    this.setData();
  },

  methods: {
    // createList() {
    //   this.list = {
    //     eatingpattern:"",
    //     breakfast:"",
    //     lunch:"",
    //     dinner:"",
    //     dessert:"",
    //     physiologicaltest1:"",
    //     physiologicaltest2:"",
    //     physiologicaltest3:"",
    //     physiologicaltest4:"",
    //     physiologicaltest5:"",
    //     physiologicaltest6:"",
    //     assessment:"",
    //   };
      
    // },
    setData() {
      this.uData = this.$store.state.uData;
      this.getNursingassessment();
      this.getKbData();
    },
    getKbData() {  //取得柯氏/巴氏(ADL)量表
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post("general/getByKey/" + this.$store.state.databaseName + "/kb",qs.stringify(postData))
        .then(rs => {
          if (rs.data.length > 0){
            this.kbData=JSON.parse(rs.data[0].datalist);
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getNursingassessment() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/nursingassessment",
          qs.stringify(postData)
        )
        .then(rs => {
          console.log(rs.data);
          if (rs.data.length > 0){
            this.nursingassessmentData = JSON.parse(rs.data[0].datalist);
            this.nursingassessmentSource = true;
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/hour72nutrition",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.length == 0) {
            //未建立資料
            this.newData = true;
            // this.createList();
            // this.addNew();
          } else {
            this.newData = false;
            var matchData = rs.data.map((i) => {
              
              if (i.datalist != "") {
                var newItem = JSON.parse(i.datalist);
                newItem.snkey = i.snkey;
                newItem.create_man = i.create_man;
                return newItem;
              }
            });
            this.list = matchData[0];
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    addNew() {
      //無資料時新增一筆資料
      this.$store.commit("getTime"); //取得現在時間
      var addData = {
        u_snkey: this.$store.state.uData.snkey,
        datalist: JSON.stringify(this.list),
        create_man:
          this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
      };

      this.axios
        .post(
          "general/add/" + this.$store.state.databaseName + "/hour72nutrition",
          qs.stringify(addData)
        )
        .then(rs => {
          if (rs.data.state == 1) {
            var pop = { msg: "已儲存", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.newData = false;
            this.getAllData();
          }
        });
    },
    editOK() {
      //修改功能
      if (this.$refs.form.validate()) {
        if (this.newData) {
          console.log('run add');
          this.addNew();
        } else {
          console.log('run edit');
          this.$store.commit("getTime"); //取得現在時間
          var postData = {
            snkey: this.list.snkey,
            datalist: JSON.stringify(this.list),
            edit_man:
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")", //執行動作的人,時間,內容
          };

          this.axios
            .post(
              "general/edit/" +
                this.$store.state.databaseName +
                "/hour72nutrition",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已儲存", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.saveNursingassessment();
              }
            });
        }
      } else {
        var pop = {
          msg: "評估日期不可為空白",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    saveNursingassessment(){
      if (this.nursingassessmentSource == false){
        //add
        this.$store.commit("getTime"); //取得現在時間
          var postData = {
            u_snkey: this.$store.state.uData.snkey,
            datalist: JSON.stringify(this.nursingassessmentData),
            create_man:
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")", //執行動作的人,時間,內容
          };

          this.axios
            .post(
              "general/add/" +
                this.$store.state.databaseName +
                "/nursingassessment",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
            });
      }else{
        //edit  
        this.$store.commit("getTime"); //取得現在時間
          var postData = {
            snkey: this.nursingassessmentData.snkey,
            datalist: JSON.stringify(this.nursingassessmentData),
            edit_man:
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")", //執行動作的人,時間,內容
          };

          this.axios
            .post(
              "general/edit/" +
                this.$store.state.databaseName +
                "/nursingassessment",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
            });
      }
    },
    print() {
      // sessionStorage.setItem("printData",JSON.stringify(this.checkedNutritionfilter));
      var postData = {
        list: this.list,
        nursingassessmentData : this.nursingassessmentData,
        kbData:this.kbData,
      }
      let routeData = this.$router.resolve({path: "/print/hour72nutritionprint",query:{data:JSON.stringify(postData)}});
      window.open(routeData.href, "_blank");
    },
  }
};
</script>


<style lang="scss">
.hour72nutrition {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>